import Link from "next/link";
import { prisma } from "~/server/db";
import { formatBookingCode } from "~/utils/booking-code";

export default async function BookingsPage() {
    const bookings = await prisma.booking.findMany({
        include: {
            team: {
                select: {
                    id: true,
                    name: true,
                },
            },
            game: {
                select: {
                    id: true,
                    name: true,
                },
            },
            instance: {
                select: {
                    startedAt: true,
                    completedAt: true,
                    currentPuzzleOrder: true,
                },
            },
        },
        orderBy: {
            createdAt: 'desc',
        },
    });

    return (
        <div>
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-bold">Bookings</h1>
                <Link
                    href="/admin/bookings/new"
                    className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                >
                    Create New Booking
                </Link>
            </div>

            <div className="rounded-lg border border-slate-800 bg-slate-900/50">
                <table className="min-w-full">
                    <thead>
                        <tr className="border-b border-slate-800">
                            <th className="text-left p-4 text-slate-200">Code</th>
                            <th className="text-left p-4 text-slate-200">Team</th>
                            <th className="text-left p-4 text-slate-200">Game</th>
                            <th className="text-center p-4 text-slate-200">Status</th>
                            <th className="text-center p-4 text-slate-200">Created</th>
                            <th className="text-center p-4 text-slate-200">Expires</th>
                            <th className="text-right p-4 text-slate-200">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {bookings.map((booking) => (
                            <tr key={booking.id} className="border-b border-slate-800 hover:bg-slate-800/50">
                                <td className="p-4">
                                    <span className="font-mono text-slate-200">{booking.code}</span>
                                </td>
                                <td className="p-4">
                                    <Link
                                        href={`/admin/teams/${booking.team.id}`}
                                        className="text-blue-400 hover:text-blue-300"
                                    >
                                        {booking.team.name}
                                    </Link>
                                </td>
                                <td className="p-4">
                                    <Link
                                        href={`/admin/games/${booking.game.id}`}
                                        className="text-blue-400 hover:text-blue-300"
                                    >
                                        {booking.game.name}
                                    </Link>
                                </td>
                                <td className="p-4 text-center">
                                    {booking.instance ? (
                                        booking.instance.completedAt ? (
                                            <span className="text-green-400">Completed</span>
                                        ) : (
                                            <span className="text-yellow-400">In Progress</span>
                                        )
                                    ) : (
                                        <span className="text-slate-400">Not Started</span>
                                    )}
                                </td>
                                <td className="p-4 text-center text-slate-400">
                                    {new Date(booking.createdAt).toLocaleDateString()}
                                </td>
                                <td className="p-4 text-center text-slate-400">
                                    {booking.expiresAt ? (
                                        new Date(booking.expiresAt).toLocaleDateString()
                                    ) : (
                                        <span className="text-slate-500">Never</span>
                                    )}
                                </td>
                                <td className="p-4 text-right">
                                    <Link
                                        href={`/admin/bookings/${booking.id}`}
                                        className="text-blue-400 hover:text-blue-300 mr-4"
                                    >
                                        Edit
                                    </Link>
                                    <button
                                        className="text-red-400 hover:text-red-300"
                                    // We'll implement delete functionality later
                                    >
                                        Delete
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
} 